'use client';
import { useState } from 'react';
import { useRouter } from 'next/navigation';

export default function AddProduct() {
  const router = useRouter();
  const [form, setForm] = useState({ name: '', price: '', imageUrl: '' });
  const [loading, setLoading] = useState(false);

  const handleChange = (e: any) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: any) => {
    e.preventDefault();
    setLoading(true);
    await fetch('/api/products', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        name: form.name,
        price: Number(form.price),
        imageUrl: form.imageUrl,
      }),
    });
    setLoading(false);
    router.push('/products');
  };

  return (
    <section className="py-10 px-4 md:px-8 min-h-[70vh] bg-gradient-to-b from-white to-pink-50 flex items-center justify-center">
      <div className="bg-white rounded-xl shadow-lg p-8 w-full max-w-md border border-gray-100">
        <h1 className="text-3xl font-extrabold text-pink-600 mb-6 text-center">Add New Product</h1>
        <form onSubmit={handleSubmit} className="grid gap-5">
          <input name="name" placeholder="Product Name" onChange={handleChange} className="border border-gray-300 p-3 rounded-lg focus:outline-none focus:ring-2 focus:ring-pink-400" required />
          <input name="price" placeholder="Price" type="number" onChange={handleChange} className="border border-gray-300 p-3 rounded-lg focus:outline-none focus:ring-2 focus:ring-pink-400" required />
          <input name="imageUrl" placeholder="Image URL" onChange={handleChange} className="border border-gray-300 p-3 rounded-lg focus:outline-none focus:ring-2 focus:ring-pink-400" required />
          <button type="submit" className="bg-pink-600 text-white px-6 py-3 rounded-lg font-semibold shadow hover:bg-pink-700 transition disabled:opacity-60" disabled={loading}>{loading ? 'Adding...' : 'Submit'}</button>
        </form>
      </div>
    </section>
  );
}